.dialog-container {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  min-height: 400px;
  background: var(--surface-color);
  border-radius: 12px;
  overflow: hidden;

  .dialog-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 24px;
    border-bottom: 1px solid var(--border-color);
    background: linear-gradient(135deg, 
      var(--primary-color) 0%, 
      var(--primary-dark-color) 100%);
    color: white;

    h2 {
      display: flex;
      align-items: center;
      gap: 12px;
      margin: 0;
      font-size: 20px;
      font-weight: 600;

      mat-icon {
        font-size: 28px;
        width: 28px;
        height: 28px;
      }
    }

    .close-btn {
      background: transparent;
      border: none;
      color: white;
      cursor: pointer;
      padding: 8px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.2s;

      &:hover:not(:disabled) {
        background: rgba(255, 255, 255, 0.2);
      }

      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }

      mat-icon {
        font-size: 24px;
        width: 24px;
        height: 24px;
      }
    }
  }

  .dialog-content {
    flex: 1;
    padding: 24px;
    overflow-y: auto;

    .section {
      margin-bottom: 24px;

      h3 {
        display: flex;
        align-items: center;
        gap: 8px;
        font-size: 16px;
        font-weight: 600;
        margin: 0 0 16px 0;
        color: var(--text-primary);

        mat-icon {
          font-size: 20px;
          width: 20px;
          height: 20px;
          color: var(--primary-color);
        }
      }

      .info-grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 16px;
        background: var(--background-color);
        padding: 16px;
        border-radius: 8px;
        border: 1px solid var(--border-color);

        .info-item {
          display: flex;
          flex-direction: column;
          gap: 6px;

          .label {
            font-size: 12px;
            color: var(--text-secondary);
            font-weight: 500;
          }

          .value {
            font-size: 14px;
            color: var(--text-primary);
            font-weight: 500;

            &.id-badge {
              display: inline-block;
              padding: 4px 10px;
              background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
              color: white;
              border-radius: 6px;
              font-family: 'Courier New', monospace;
              font-size: 13px;
              font-weight: 600;
              letter-spacing: 0.5px;
              width: fit-content;
            }

            &.amount {
              font-size: 18px;
              font-weight: 700;
              color: var(--primary-color);
            }
          }
        }
      }
    }

    .bank-info {
      .bank-details {
        background: linear-gradient(135deg, #667eea15 0%, #764ba215 100%);
        border: 2px solid var(--primary-color);
        border-radius: 12px;
        padding: 20px;

        .bank-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 12px 0;

          &:not(:last-child) {
            border-bottom: 1px solid var(--border-color);
          }

          .bank-label {
            font-size: 14px;
            font-weight: 600;
            color: var(--text-secondary);
          }

          .bank-value {
            font-size: 15px;
            font-weight: 600;
            color: var(--text-primary);

            &.account-number {
              font-family: 'Courier New', monospace;
              font-size: 16px;
              letter-spacing: 1px;
              color: var(--primary-color);
            }
          }
        }
      }
    }

    .notice-box {
      display: flex;
      gap: 12px;
      padding: 16px;
      background: #fff3cd;
      border: 1px solid #ffc107;
      border-radius: 8px;
      margin-top: 20px;

      mat-icon {
        flex-shrink: 0;
        color: #ff9800;
        font-size: 24px;
        width: 24px;
        height: 24px;
      }

      .notice-content {
        flex: 1;

        p {
          margin: 0 0 8px 0;
          font-size: 14px;
          font-weight: 600;
          color: #856404;
        }

        ul {
          margin: 0;
          padding-left: 20px;
          list-style-type: none;

          li {
            position: relative;
            font-size: 13px;
            color: #856404;
            margin-bottom: 6px;
            padding-left: 8px;

            &:before {
              content: 'âœ“';
              position: absolute;
              left: -12px;
              font-weight: bold;
              color: #ff9800;
            }
          }
        }
      }
    }

    .success-box {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 24px;
      background: linear-gradient(135deg, #4caf5015 0%, #2e7d3215 100%);
      border: 2px solid #4caf50;
      border-radius: 12px;
      margin-top: 20px;
      animation: fadeIn 0.3s ease-in;

      mat-icon {
        font-size: 48px;
        width: 48px;
        height: 48px;
        color: #4caf50;
        margin-bottom: 12px;
        animation: scaleIn 0.4s ease-out;
      }

      p {
        margin: 0 0 8px 0;
        font-size: 16px;
        font-weight: 600;
        color: #2e7d32;
      }

      small {
        font-size: 13px;
        color: #66bb6a;
      }
    }
  }

  .dialog-footer {
    display: flex;
    gap: 12px;
    padding: 16px 24px;
    border-top: 1px solid var(--border-color);
    background: var(--background-color);

    button {
      flex: 1;
      padding: 12px 24px;
      border: none;
      border-radius: 8px;
      font-size: 14px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.2s;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;

      mat-icon {
        font-size: 20px;
        width: 20px;
        height: 20px;
      }

      mat-spinner {
        margin-right: 8px;
      }

      &.btn-cancel {
        background: var(--surface-color);
        color: var(--text-primary);
        border: 1px solid var(--border-color);

        &:hover:not(:disabled) {
          background: var(--background-color);
          transform: translateY(-1px);
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
        }
      }

      &.btn-confirm {
        background: linear-gradient(135deg, #4caf50 0%, #2e7d32 100%);
        color: white;
        box-shadow: 0 4px 12px rgba(76, 175, 80, 0.3);

        &:hover:not(:disabled) {
          transform: translateY(-2px);
          box-shadow: 0 6px 20px rgba(76, 175, 80, 0.4);
        }
      }

      &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
        transform: none !important;
      }
    }
  }
}

// Animations
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

// Dark mode adjustments
:host-context(.dark-theme) {
  .dialog-container {
    .notice-box {
      background: rgba(255, 152, 0, 0.15);
      border-color: #ff9800;
    }
  }
}

// Responsive
@media (max-width: 600px) {
  .dialog-container {
    .dialog-content {
      .section {
        .info-grid {
          grid-template-columns: 1fr;
        }
      }
    }

    .dialog-footer {
      flex-direction: column;

      button {
        width: 100%;
      }
    }
  }
}
